body {
    background-color: rgba(31, 37, 43)
}

.movies {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  align-items: start;
}

.movie { 
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.movie__rating {
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: center;

  color: white;
  width: 50px;
  height: 25px;
  line-height: 13px;
  border-radius: 25px;
  top: 8px;

  background-color: rgb(255, 169, 13);
}

img {
  width: 100%;
  min-width: 170px;
  border-radius: 3px;
  box-shadow: 20px 0px 20px #222;
}

.movie__data {
  display: flex;
  width: 100%;
  flex-direction: column;
}

.movie__title {
  color: white;
  font-size: 16px;
  margin: 0;
  margin-top: 20px;
  margin-bottom: 7px;
}

.movie__genres {
  display: flex;
}

.genre {
  color: rgb(135, 160, 175);
  margin: 0;
  margin-right: 5px;
  
}